  import {mapActions } from 'vuex'
	export default {
		data() {
			return {
				username: '',
        password: ''
			}
		},
    computed: {
      formReady() {
        return true
        return (
          this.username.length >= 3 &&
          this.password.length >= 6
        )
      }
    },
		methods: {
      ...mapActions(['login']),
      
      submit() {
        let params = {
          username: this.username,
          password: this.password
        }
        
        this.attempLogin(params)
      },
      async attempLogin(params = {}) {
        // code 只能使用一次，所以每次单独调用
        let loginCode = await uni.login({provider: 'weixin'})
        params.code = loginCode[1].code
        
        uni.showLoading({
            title: '正在登录...'
        })
        
        try {
          await this.login(params)
          
          uni.showToast({
            title: '欢迎回来！',
            duration: 2000
          })
          
          uni.navigateBack()
        } catch (e) {
          if (e.status === 401) {
            uni.showToast({
              title: '用户名或密码错误！',
              icon: 'none',
              duration: 2000
            })
          }
        }
        
        uni.hideLoading()
      }
		},
    onShow() {
      // 页面打开时使用 code 自动登录一次
      this.attempLogin()
    }
	}
